section {
  scroll-margin-top: 100px;
}

span {
  color: #38bdf8;
}

/** Media Queries **/
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

body {
  background: linear-gradient(135deg, #06196e 0%, #0c1b3f 50%, #041975 100%);
  color: #e5e7eb;
  width: 100%;
  font-family: "Inter", system-ui, -apple-system, sans-serif;
}

.header {
  transition: background 0.3s ease, box-shadow 0.3s ease;
  border-bottom: 2px solid #1e293b;
  justify-content: space-between;
  text-align: center;
  margin: 0;
  padding: 1rem;
  position: sticky;
  top: 0;
}
@media (min-width: 769px) {
  .header {
    display: flex;
  }
}

.header--scroll {
  background: linear-gradient(135deg, #06196e 0%, #0c1b3f 50%, #041975 100%);
  backdrop-filter: blur(12px);
  opacity: 1;
}

.logo_name {
  display: flex;
  text-align: center;
  justify-content: center;
}
.logo_name a {
  opacity: 0.4;
  color: #ffffff;
  display: inline-block;
  text-decoration: none;
  font-size: 3rem;
  padding: 1rem;
  font-weight: bold;
}

.navegacion {
  padding: 2rem 1rem;
}
.navegacion a {
  text-decoration: none;
  color: #e5e7eb;
  font-size: 1.5rem;
  padding: 0 0.5rem;
  font-weight: bold;
  transition: font-size 0.3s ease-in-out;
}
.navegacion a:hover {
  color: rgb(70.7800970874, 193.9019417476, 248.5199029126);
  cursor: pointer;
  font-size: 1.7rem;
}